function formatRelativeTime(dateString) {
  if (!dateString) return
  const past = new Date(dateString).getTime()
  const now = new Date().getTime()
  const relative = now - past
  // return ,if invalid
  if (isNaN(past)) {
    console.log(``)
    return dateString
  }
  // return without handle if confused
  if (relative < 0) {
    console.log(``)
    return dateString
  }

  const years = relative / (1000 * 3600 * 24 * 30 * 12)
  if (years > 1) return `${Math.floor(years)} `
  const months = years * 12
  if (months > 1) return `${Math.floor(months)} `
  const days = months * 30
  if (days > 1) return `${Math.floor(days)} `
  const hours = days * 24
  if (hours > 1) return `${Math.floor(hours)} `
  const mins = hours * 60
  if (mins > 1) return `${Math.floor(mins)} `
  if (mins < 1) return ''

  console.log('')
  return dateString
}
export default formatRelativeTime