import {
  AuthContainer,
  AuthInputContainer,
  AuthButton,
  AuthLinkText,
  AuthLinkTextGroup,
} from "../components/common/auth.styled";
import AuthInput from "../components/AccountForm/AuthInput";
import { ACLogoIcon } from "../assets/icons";
import { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { Container } from "react-bootstrap";
import { userLogin } from "../API/auth";
import Swal from "sweetalert2";

const LoginPage = () => {
  const [account, setAccount] = useState("");
  const [password, setPassword] = useState("");
  const navigate = useNavigate();

  const handleClick = async () => {
    if (account.length === 0) {
      return;
    }
    if (password.length === 0) {
      return;
    }

    const data = await userLogin({
      account,
      password,
    });
    const token = data.data.token;

    if (data.success) {
      // 
      localStorage.setItem("authToken", token);
      Swal.fire({
        position: "top",
        title: "",
        timer: 1000,
        icon: "success",
        showConfirmButton: false,
      });
      navigate("/main");
      return;
    } else if (data.data.user.role === "admin") {
      Swal.fire({
        position: "top",
        title: "",
        timer: 1000,
        icon: "error",
        showConfirmButton: false,
      });
      // 
      setAccount("");
      setPassword("");
    } else {
      Swal.fire({
        position: "top",
        title: '',
        timer: 1000,
        icon: "error",
        showConfirmButton: false,
      });
      // 
      setAccount("");
      setPassword("");
    }
  };

  return (
    <Container>
      <AuthContainer>
        <div>
          <ACLogoIcon />
        </div>
        <h2 style={{ margin: "20px 0 10px 0" }}>Alphitter</h2>
        <AuthInputContainer>
          <AuthInput
            label=""
            placeholder=""
            value={account}
            onChange={(accountInputValue) => setAccount(accountInputValue)}
          />
        </AuthInputContainer>
        <AuthInputContainer>
          <AuthInput
            type="password"
            label=""
            placeholder=""
            value={password}
            onChange={(passwordInputValue) => setPassword(passwordInputValue)}
          />
        </AuthInputContainer>
        <AuthButton onClick={handleClick}></AuthButton>
        <AuthLinkTextGroup>
          <Link to="/signup">
            <AuthLinkText></AuthLinkText>
          </Link>{''}
          <Link to="/admin_login">
            <AuthLinkText></AuthLinkText>
          </Link>
        </AuthLinkTextGroup>
      </AuthContainer>
    </Container>
  );
};

export default LoginPage;
